import { Handler } from "@netlify/functions";
const { NetlifyJwtVerifier } = require("@serverless-jwt/netlify");

const verifyJwt = NetlifyJwtVerifier({
    issuer: "https://dev-boefz5w6.us.auth0.com/",
    audience: "https://dev-boefz5w6.us.auth0.com/api/v2/"
});

const MongoClient = require("mongodb").MongoClient;

const MONGODB_URI =
    "mongodb://u631jclyyphjapvexkrl:4EGhHjksJpJYDOecE6zF@bd7tfidpxkvovkr-mongodb.services.clever-cloud.com:27017/bd7tfidpxkvovkr";
const DB_NAME = "bd7tfidpxkvovkr";

let cachedDb = null;

const connectToDatabase = async (uri) => {
    // we can cache the access to our database to speed things up a bit
    // (this is the only thing that is safe to cache here)
    if (cachedDb) return cachedDb;

    const client = await MongoClient.connect(uri, {
        useUnifiedTopology: true
    });

    cachedDb = client.db(DB_NAME);

    return cachedDb;
};

const queryDatabase = async (db) => {
    const pokemon = await db.collection("pokemon").find({}).toArray();

    return {
        statusCode: 200,
        headers: {
            "Content-Type": "application/json"
        },
        body: JSON.stringify(pokemon)
    };
};

const handler: Handler = verifyJwt(async (event, context) => {
    // otherwise the connection will never complete, since
    // we keep the DB connection alive
    context.callbackWaitsForEmptyEventLoop = false;

    const db = await connectToDatabase(MONGODB_URI);
    return queryDatabase(db);
});

export { handler };
